import { useEffect } from 'react'
import Fret from './Fret.js'
import { Midi } from "@tonaljs/tonal";

const String = props => {


    const {stringNote, fretSpacing, height, synth, Tone } = props
    const frets = fretSpacing.length
    const stringNotes = []

    for (let i=0; i < frets; i++) {
        stringNotes.push([Midi.midiToNoteName(stringNote + i), fretSpacing[i]])
    }

    const stringFrets = stringNotes.map((note) => {
        return (
        <li key={note} style={{ flexGrow: note[1], flexShrink: '0', flexBasis: '0', height: height }}><Fret note={note[0]} synth={synth} Tone={Tone}/></li>
        )
    })
    
    return (
        <ul className='string'>{stringFrets}</ul>
    )
}

export default String