import { React, useEffect, useRef } from 'react'
import useCanvas from './useCanvas'
import predraw from './predraw'
import postdraw from './postdraw'


const Canvas = props => {  

  const { draw, setHeight, options, ...rest } = props
  const { context, ...moreConfig } = options
  const canvasRef = useRef(null)

  useEffect(() => {
    setHeight(canvasRef.current.getBoundingClientRect().height)
  })
  useCanvas(draw, {context}, {predraw,postdraw}, canvasRef)

  return (
      <canvas ref={canvasRef} {...rest}>Sorry, your browser needs to support canvas in order to run this web page.</canvas>
    )
}

export default Canvas